<script>

  export let id;
  export let name;
  export let abils;
  export let description;
  export let img;

  const imgsrc = 'build/img/';

</script>

<div class="presentation-box">
  <div class="info-box">
    <label for="deploy-{id}" class="img-box" style="background-image: url({imgsrc + img});">
    <input type="checkbox" id="deploy-{id}">
    <div class="description-box">
      <br>
      <b>
        {#each abils as abil}
          {abil} <br>
        {/each}
      </b>
      <br>
      {@html description}
    </div>
    </label>
  </div>
  <br>
  <h3>{name}</h3>
</div>

<style type="text/scss">

  $fontSize: 15px;
  $cardWidth: 250px;

	$gafColor: #018DFF;
	$gasColor: #E1E1E1;
  $primaryColor: mediumslateblue;
  
  *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: $fontSize;
    --fontSize: font-size;
  }

  .presentation-box{
    width: $cardWidth;
    height: $cardWidth + $fontSize * 3;
    text-align: center;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: -1px 1px 2px 2px rgba($color: #000000, $alpha: 0.2);
    .info-box{
      position: relative;
      height: $cardWidth;
      cursor: pointer;
      input{
        display: none;
      }
      .img-box{
        display: block;
        width: 100%;
        height: 100%;
        background-size: 100%;
        background-position: center;
        z-index: 1;
        cursor: pointer;
      }
      .description-box{
        position: absolute;
        top: 0;
        left: 0;
        padding: 10px;
        width: 100%;
        height: 100%;
        color: white;
        background-color: rgba($color: #3D3F99, $alpha: 0.8);
        z-index: 2;
        transition: 500ms;
        transform: translateX(-100%);
        cursor: pointer;
      }
    }
  }

  @media(hover: none){
    .presentation-box{
      .info-box{
        input:checked ~ .description-box{
          transform: translateX(0);
        }
      }
    }
  }

  @media(hover: hover){
    .presentation-box{
      .info-box:hover, .info-box:active{
        .description-box{
          transform: translateX(0);
        }
      }
    }
  }

</style>