<script>

	import { spring } from 'svelte/motion';
	import { pannable } from './Components/pannable.js';

	import Desc from './Components/Description.svelte';
	import Memb from './Components/Members.svelte';
	import Proj from './Components/Projects.svelte';
	import Tech from './Components/Technologies.svelte';
	import Caro from './Components/Carousel.svelte';

	let width = 60;
	let height = 60;

	const coords = spring({ x: 0, y: 0 }, {
		stiffness: 0.2,
		damping: 0.4
	});

	function handlePanStart() {
		width = height = 350;
		coords.stiffness = coords.damping = 1;
	}

	function handlePanMove(event) {
		coords.update($coords => ({
			x: $coords.x + event.detail.dx,
			y: $coords.y + event.detail.dy
		}));
	}

	function handlePanEnd(event) {
		width = height = 60;
		coords.stiffness = 0.2;
		coords.damping = 0.4;
		coords.set({ x: 0, y: 0 });
	}

</script>

<svelte:head>
	<link rel="stylesheet" href="css/fontello.css">
</svelte:head>


<header>
	<nav class="logo-box">
		<div class="logo box"
			use:pannable
			on:panstart={handlePanStart}
			on:panmove={handlePanMove}
			on:panend={handlePanEnd}
			style="
				transform: translate({$coords.x}px,{$coords.y}px) rotate({$coords.x * 0.2}deg);
				--width: {width + 'px'};
				--height: {height + 'px'};
		"></div>
		<div class="logo-bar"></div>
	</nav>
	<div class="logo-ghost"></div>
	<div class="content">
		<ul class="h-list header-box">
			<li class="big-logo">
				<img src="build/img/logo.png" alt="GA Entertainment">
			</li>
			<li class="ide-box">
				<h1>
					<span>GA</span> <br>
					Innovación <br>
					Desarrollo <br>
					Entretenimiento <br>
				</h1>
			</li>
		</ul>
	</div>
</header>

