<script>

	import Desc from './Description.svelte';
	import Pres from './Presentation.svelte'

	let scrollopacity = 0.0;

	let fin, fout;

	function mouseenter(e){
		clearInterval(fout);
		fin = setInterval(function(){
			if(scrollopacity >= 1.0){
				scrollopacity = 1.0;
				clearInterval(fin);
			}
			scrollopacity += 0.1;
		}, 50);
	}
	
	function mouseleave(e){
		clearInterval(fin);
		fout = setInterval(function(){
			if(scrollopacity <= 0.0){
				scrollopacity = 0.0;
				clearInterval(fout);
			}
			scrollopacity -= 0.1;
		}, 50);
	}

